:root {
    --primary: #34ebf8;
    --background: rgba(211, 209, 209, 0.658);
}

* {
    font-family: sans-serif;
    font-weight: bold;
}

body {
    
    overflow-y: hidden;
}


main {
    

    display: flex;
    justify-content: center;
    align-items: center;
    
    flex-direction: column;
    
    margin-top: 5vh;
    
    

}

#topBar {
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;

    width: 100%;

    text-align: center;
    font-size: 20px;
    color: var(--primary)

}

#mainContent {
    background-color: var(--background);
    

    
    display: flex;
    flex-direction: column;
    
    align-items: center;
    padding: 20px 0;

    
}

label {
    margin-right: 10px;
    display: inline-block;    
    font-size: 20px;
    width: 200px;
    
}


input {
    border-radius: 10px;
    border: 2px solid lightgray;
    font-size: larger;
    padding: 10px 15px;
}

button {
    margin-top: 15px;
    color: var(--primary);
    font-size: 20px;
    border: none;
    background-color: rgb(138, 136, 136);
    border-radius: 10px;
    padding: 15px 25px;

    transition-duration: 500ms;
}

button:hover {
    background-color: rgb(117, 117, 117);
    transition-duration: 500ms;
    border-radius: 15px;
}

button:active {
    background-color: rgb(138, 136, 136);
    transition-duration: 500ms;
    border-radius: 20px;
}
 
hr {
    border: 1px solid grey;
    width: 80%;
    border-radius: 20px;
}

#owing {
    width: 100%;
    text-align: center;
    background-color: var(--background);
}



footer {
    text-align: center;
    color: var(--primary);
    width: 100%;
    padding: 15px 0px;
    font-size: 20px;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
}

.blackBackground {
    background-color: black;
    opacity: 0.9;
    
}

.inputField {
    margin-top: 10px;
}

/* Desktop */
@media only screen and (min-width: 1000px) {
    
    main {
        width: 50%;
        margin: auto;
        margin-top: 10vh;
    }

    #mainContent {
        width: 100%;
    }
    
    input {
        padding: 10px;
    }

    #owing h3 {
        font-size: 25px;
    }

    
}

/* Mobile */
@media only screen and (max-width: 1000px) {

    main {
        margin: 5vh 10px;
    }

    #mainContent {
        padding: 10px 10px;
        text-align: center;
        
    }

    input {
        width: 80%;
        height: 30px;
    }

    #owing h3 {
        font-size: 20px;        
    }
}
